<template>
    <div>
        <base-card mode="outline">
            <h1>Contact coach</h1>
            <form @submit.prevent="submitForm">
                <div class="form-control" :class="{ invalid: !emailIsValid }">
                    <label for="email">Your E-Mail</label>
                    <input type="email" name="email" id="email" placeholder="test@test.com" v-model.trim="email" />
                    <h2>{{ email.isValid }}</h2>
                </div>
                <div class="form-control" :class="{ invalid: !messageIsValid }">
                    <label for="message">Message</label>
                    <textarea name="message" id="message" cols="30" rows="5" v-model.trim="message"></textarea>
                    <p v-if="!formIsValid" class="errors">
                        Please enter a valid email and non-empty message.
                    </p>
                </div>
                <base-button class="btn-center">Send Message</base-button>
            </form>
        </base-card>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    emits: ['save-request'],
    data()
    {
        return {
            email: '',
            message: '',
            formIsValid: true,
            emailIsValid: true,
            messageIsValid: true
        };
    },
    computed: {
        coachDetails()
        {
            return this.coaches.find(coach => coach.id === this.userId);
        },
        ...mapGetters(['coaches', 'userId'])
    },
    methods: {
        submitForm()
        {
            this.formIsValid = true;
            this.emailIsValid = true
            this.messageIsValid = true
            if (
                this.email === '' ||
                !this.email.includes('@') ||
                this.message === ''
            ) {
                this.formIsValid = false;
                this.emailIsValid = false
                this.messageIsValid = false
                return;
            }
            this.$store.dispatch('contactCoach', {
                email: this.email,
                message: this.message,
                coachId: this.$route.params.id,
                to: this.fullName

            });
            this.$router.replace('/coaches');
        },
    },
}
</script>
<style scoped>
form {
    margin: 1rem;
    border: 1px solid #ccc;
    border-radius: 12px;
    padding: 1rem;
}

.form-control {
    margin: 0.5rem 0;
}

label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

input,
textarea {
    display: block;
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    padding: 0.15rem;
}

input:focus,
textarea:focus {
    border-color: #3d008d;
    background-color: #faf6ff;
    outline: none;
}

.btn-center {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.errors {
    font-weight: bold;
    color: red;
}

.actions {
    text-align: center;
}

.invalid label {
    color: red;
}

.invalid input,
.invalid textarea {
    border: 1px solid red;
}
</style>